.header-button{
  width: 6vw;
  height: 4vh;
  text-decoration: none;
  color: #FFFFFF;
  font-family: iran-yekan, serif;
  font-size: 1.2vw;
  line-height: 0.5vw;
  transition: 0.5s;
}

.header-button:hover{
  color: #7AB8FF
}

@media screen and (max-width: 500px){
   .header-button{
    margin-top: 3.125vh;
    height: 5.46vh;
    width: 50vw;
    font-size: 5.5vw;
  }
}
